body {
  text-align: center;
  background-color: lime;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#display {
  background-color: black;
  color: white;
  width: 400px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#one,#two,#three,#four,#five,#six,#seven,#eight,#nine,#zero {
  background-color: slateblue;
}
#clear {
  background-color: crimson;
}
#equals {
  background-color: gray;
}
#add, #subtract, #multiply, #divide, #decimal {
  background-color: yellow;
}
.Pod {
  color: black;
  display: flex;
  justify-content: center;
  align-content: center;
  border: 2px lime solid;
  grid-area: pod;
}
#one {
  grid-area: one;
}
#two {
  grid-area: two;
}
#three {
  grid-area: three;
}
#four {
  grid-area: four;
}
#five {
  grid-area: five;
}
#six {
  grid-area: six;
}
#seven {
  grid-area: seven;
}
#eight {
  grid-area: eight;
}
#nine {
  grid-area: nine;
}
#zero {
  grid-area: zero;
}
#decimal {
  grid-area: decimal;
}
#add {
  grid-area: add;
}
#subtract {
  grid-area: subtract;
}
#multiply {
  grid-area: multiply;
}
#divide {
  grid-area: divide;
}
#clear {
  grid-area: clear;
}
#equals {
  grid-area: equals;
}
.button-grid {
  display: grid;
  grid-template-rows: repeat(5, 80px);
  grid-template-columns: repeat(5, 80px);
  grid-template-areas: 
  "clear clear clear divide multiply"
  "seven eight nine subtract add"
  "four five six equals equals"
  "one two three equals equals"
  ". zero decimal equals equals"
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
  pointer-events: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
